//
const repeatList = [{
		value: '1O',
		label: ''
	},
	{
		value: '1D',
		label: ''
	},
	{
		value: '1W',
		label: ''
	},
	{
		value: '1M',
		label: ''
	},
	{
		value: '1Y',
		label: ''
	}
];
const remindList = [{
		value: '-1',
		label: ''
	},
	{
		value: '0',
		label: ''
	},
	{
		value: '1',
		label: ''
	},
	{
		value: '3',
		label: ''
	},
	{
		value: '7',
		label: ''
	}
];
/**
 * 
 * 
 * @param {Object} repeat
 */
function promiseRepeatText(repeat) {
	// console.info(repeat);
	let data= repeatList.find(item => item.value == repeat)
	return data.label;
};
/**
 * 
 * @param {Object} remind
 */
function promiseRemindText(remind) {
	// console.info(remind);
	let data= remindList.find(item => item.value == remind)
	// console.info(data);
	return data.label;
};
export default {
	repeatList,
	remindList,
	promiseRepeatText,
	promiseRemindText,
}
