// VueVue()vmmain.jsVue.use(httpInterceptor, app)
// Vue"this"
// installhttps://uviewui.com/components/vueUse.html
const install = (Vue, vm) => {
	// 
	Vue.prototype.$u.http.setConfig({
		baseUrl: Vue.prototype.$pig.constData.baseUrl, // 
		method: 'POST',
		// jsonJSON.parse()
		dataType: 'json',
		showLoading: false, // loading
		loadingText: '...', // loading
		loadingTime: 800, // ms
		originalData: false, // 
		loadingMask: true, // loading
		// 
		header: {
			'content-type': 'application/json;charset=UTF-8'
		},
	});


	// 
	Vue.prototype.$u.http.interceptor.request = (config) => {
		// token
		// vuextokenuViewvuex
		// https://uviewui.com/components/globalVariable.html
		// config.header.token = vm.token;

		// uViewvuex$store.state
		config.header.Authorization = vm.$store.state.user.token;

		// tokenglobalDatagetApp().globalData
		// config.header.token = getApp().globalData.username;

		// tokenStorage
		// Storage
		// const token = uni.getStorageSync('token');
		// config.header.token = token;

		// urlurlthis.$u.get(url)url
		// if (config.url == '/user/login') config.header.noToken = true;
		// configreturn
		return config;
		// returnfalse
		// if(config.url == '/user/rest') return false; // 
	}

	// 
	Vue.prototype.$u.http.interceptor.response = (res) => {
		console.debug(":" + res)
		if (res.code == 1) {
			// rescoderesult
			// res.resultthis.$u.post(url).then(res => {})thenres
			// originalDatatrue
			return res.data;
		} else if (res.code == 201) {
			// 201token
			vm.$u.toast('');
			setTimeout(() => {
				// uView
				vm.$u.route('/pages/loveStart/loveStart')
			}, 1500)
			return false;
		} else if (res.code == 40003) {
			vm.$u.toast('?');
			setTimeout(() => {
				// uView
				vm.$u.route('/pages/loveStart/loveStart')
			}, 1500)
			return false;
		} else if (res.code == 403) {
			vm.$u.toast(':!');
			return false;
		} else {
			// falsePromisereject
			// this.$u.post(url).then().catch(res=>{})catchres
			return false;
		}
	}
}

export default {
	install
}
