/**
 * 
 * u-loadmore
 * @param {Object} dataCount
 */
function getloadMoreTip(dataCount) {
	if (dataCount == 0) {
		return 'nomore';
	} else {
		return 'loadmore';
	}
};
function random(min, max) {
	if (max > 0 && max >= min) {
		let gab = max - min + 1;
		return Math.floor(Math.random() * gab + min);
	} else {
		return 0;
	}
}
export default {
	getloadMoreTip,
	random
}
