import Vue from 'vue'
export default {
	state: {
		token: '',
		isLogin: false,
		isNewUser: false,
		isOnwer: false,
		loginUser: {
			gender: 1,
			nickname: '',
			motto: '~',
			avatar: 'https://lovestatic.jokeo.cn/202106/222.jpg',
		}
	},
	getters: {
		getToken: state => {
			return state.token
		},
		getLoginUser: state => {
			return state.loginUser
		},
		getLoginUserId: state => {
			return state.loginUser.id
		},
		getIsLogin: state => {
			return state.isLogin
		},
		getIsNewUser: state => {
			return state.isNewUser
		}
	},
	mutations: {
		modifyToken(state, payload) {
			state.token = payload;
			uni.setStorageSync('token', payload)
		},
		modifyLoginUser(state, payload) {
			state.isLogin = true;
			state.loginUser = payload;
			uni.setStorageSync('userInfo', payload)
		},
		modifyIsNewUser(state, payload) {
			state.isNewUser = payload;
		}
	},
	actions: {
		updateLoginUser(context, userInfo) {
			context.commit('modifyLoginUser', userInfo);
			context.commit('modifyLoveStartId', userInfo.loveId);
		},
		//
		login(context) {
			return new Promise((resolve, reject) => {
				wx.getUserProfile({
					desc: '',
					success: userRes => {
						console.log(userRes);
						uni.login({
							provider: 'weixin',
							success: function(res) {
								if (res.code) {
									let param = {
										appid: Vue.prototype.appid,
										code: res.code,
										nickname: userRes.userInfo.nickName,
										avatar: userRes.userInfo.avatarUrl
									};
									Vue.prototype.$u.api.wechatRegister(param).then(
										res => {
											console.info(res);
											resolve("login");
											context.commit('modifyToken', res
												.token);
											context.commit('modifyLoginUser', res
												.loginUser);
										});
								} else {
									Vue.prototype.$u.toast("");
									reject("code");
								}
							}
						});
					},
					fail: res => {
						Vue.prototype.$u.toast("");
						console.log(res);
						reject("code");
					}
				});

				// resolve("login");
				// Vue.prototype.$u.toast("code");
				// reject("code");
			})
		},

		// 1.
		getWxUserInfo() {
			wx.getUserProfile({
				desc: '',
				success: res => {
					// console.log(res);
					this.wxlogin(res.userInfo);
				},
				fail: res => {
					console.log(res);
				}
			});
		},
		//2.code
		wxlogin(userInfo) {
			let that = this;
			uni.login({
				provider: 'weixin',
				success: function(res) {
					if (res.code) {
						that.wechatRegistered(res.code, userInfo);
					} else {
						console.log('code!!');
					}
				}
			});
		},
		//3.
		wechatRegistered(code, userInfo) {
			let that = this;
			let param = {
				appid: this.appid,
				code: code,
				nickname: userInfo.nickName,
				gender: userInfo.gender,
				avatar: userInfo.avatarUrl
			};
			this.$u.api.wechatRegister(param).then(res => {
				console.info(res);
				context.commit('modifyToken', res.token);
				context.commit('modifyLoginUser', res.loginUser);
			});
		},
	}
}
